export default {
  name: 'catgory',
  props:['value'],
  data () {
    return {
      
    }
  },
  methods: {
    goToActivity(type) {
      let id = this.value[type].ACTIVITY_ID;
      this.$store.commit('setId',{key:'activityId',value:id});
      this.$router.push({path:'/mainpage/home/allActivity/actDetail/' + id});
    },
  },
  created () {
  }
}



