export default {
  name: 'homeCell',
  props:['cellData'],
  data () {
    return {
      Text_l: this.cellData.Text_l,
      Text_r: this.cellData.Text_r,
      type: this.cellData.type
    }
  },
  methods: {
    goToMoreFn() {
      let type = this.type;
      this.$router.push({path:type});            
    }
  }
}



